@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app {
  display: grid; 
  place-items: center;
  background-color: #dadbd3;
  height: 100vh;
}

.app_body {
  display: -webkit-flex;
  display: flex;
  background-color: #ededed;
  margin-top: -30px;
  height: 90vh;
  width: 120vh;
  box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.7);
}
.sidebarChat {
    display: -webkit-flex;
    display: flex;
    padding: 10px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
}

a {
    text-decoration: none !important;
    color: black;
}

.sidebarChat:hover {
    background-color: #ebebeb;
}

.sidebarChat_info > h2 {
    font-size: 16px;
    margin-bottom: 8px;
}

.sidebarChat_info {
    margin-left: 15px;
}
.sidebar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 0.35 1;
            flex: 0.35 1;
}

.sidebar_search {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}

.sidebar_searchContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: #fff;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar_searchContainer > .MuiSvgIcon-root {
    color: gray;
    padding: 10px;
}

.sidebar_searchContainer > input {
    border: none;
    margin-left: 10px;
}

.sidebar_header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar_headerRight {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    min-width: 10vw;
}

.sidebar_headerRight > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px !important;
}
.sidebar_chats {
    -webkit-flex: 1 1;
            flex: 1 1;
    overflow: scroll;
    background-color: white;
}
.chat {
    -webkit-flex: 0.65 1;
            flex: 0.65 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.chat_header {
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat_headerInfo {
    -webkit-flex: 1 1;
            flex: 1 1;
    padding-left: 20px;
}

.chat_headerInfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat_headerInfo > p {
    color: gray;
}

.chat_headerRight {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    min-width: 100px;
}

.chat_body {
    -webkit-flex: 1 1;
            flex: 1 1;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;
}

.chat_message {
    position: relative;
    font-size: 16px;
    padding: 10px;
    border-radius: 10px;
    background-color: #fff;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-bottom: 30px;
}

.chat_reciever {
    margin-left: auto;
    background-color: #dcf8c6;
}

.chat_name {
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}

.chat_timestamp {
    margin-left: 10px;
    font-size: xx-small;
    color: gray;
}

.chat_footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}

.chat_footer > form {
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
}

.chat_footer > form > input {
    -webkit-flex: 1 1;
            flex: 1 1;
    border-radius: 30px;
    outline: none;
    padding: 10px;
    border: none;
}

.chat_footer > form > button {
    display: none;
}

.chat_footer > .MuiSvgIcon-root {
    padding: 10px;
    color: gray;
}
.login {
    height: 100vh;
    width: 100%;
}

.login_container {
    position: relative;
    padding: 50px;
    margin-top: 150px;
    margin-left: 20px;
}

.login_text > h1 {
    font-family: 'Montserrat', sans-serif;
    color: #bdfdd0;
    font-size: 60px;
    margin-bottom: 50px;
}

.login_text > p {
    font-family: 'Noto Sans', sans-serif;
    color: #bdfdd0;
    font-size: 20px;
    width: 500px;
    margin-top: 20px;
}

.login_container > button {
    margin-top: 50px;
    text-transform: inherit;
    background-color: #043028;
    color: #bdfdd0;
    font-family: 'Noto Sans', sans-serif;
    padding: 10px;
    transition: 0.5s ease;
}

.login_container >  button:hover {
    background-color: #0f5a4c;
}


